/**** Colors ****/
/**** Fonts ****/
/**** Images ****/
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700;900&display=swap");
@font-face {
  font-family: "Roboto", sans-serif;
  src: url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700;900&display=swap");
}
@font-face {
  font-family: "Poppins", sans-serif;
  src: url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
}
/**** Global ****/
html,
body {
  width: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  font-family: "Roboto", sans-serif;
  font-size: 10px;
}

/**** Components ****/
.btn {
  padding: 20px;
  background: #ff4363;
  color: white;
  border: none;
  border-radius: 90px;
  font-size: 2.5rem;
  transition: 1s;
  width: fit-content;
  font-weight: 300;
  cursor: pointer;
}
.btn i {
  margin-right: 5px;
}
.btn:hover {
  background: #ff4363;
  color: white;
  transition: 1s;
}

.btn--white {
  background: white;
  color: #ff4363;
}
.btn--white:hover {
  background: #ff4363;
  color: white;
  transition: 1s;
}

.btn--transparent {
  background: transparent;
}

.hero {
  width: 100vw;
  min-height: 106vh;
  background-color: #ff4363;
  background-image: url("./img/heroImage.png");
  background-repeat: no-repeat;
  background-size: 70%;
  background-size: 60%;
  background-position: 115% 74%;
}
.hero__content {
  padding-top: 11%;
  padding-left: 13.5%;
  width: 40%;
  color: white;
}
.hero__title {
  font-size: 8.2rem;
  font-weight: 700;
  color: white;
  line-height: 1.3em;
  margin-bottom: 30px;
}
.hero__p {
  margin-top: 0;
  font-size: 2.1rem;
  line-height: 1.2em;
}
.hero__buttonsWrapper {
  margin-top: 50px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 70%;
}
.hero__buttonsWrapper .btn--transparent {
  margin-left: 30px;
  font-size: 2.1rem;
  font-weight: bold;
}

.advantages {
  font-family: "Poppins", sans-serif;
  display: flex;
  flex-wrap: wrap;
  place-content: center;
  place-items: center;
  grid-template-columns: 1fr 1fr 1fr;
  margin-top: 7%;
  gap: 2%;
}
.advantages .advantagesBox {
  text-align: center;
  width: 23%;
  padding-top: 6%;
  box-sizing: border-box;
  box-shadow: 0px 0px 30px 0px #707070;
  border-radius: 10px;
}
.advantages .advantagesBox__title {
  font-size: 2.4rem;
  margin-top: 9%;
  font-weight: 700;
  margin-bottom: 10px;
}
.advantages .advantagesBox__p {
  font-size: 1.6em;
  line-height: 3em;
  width: 70%;
  margin-inline: auto;
  margin-top: 0;
}

.design {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 6%;
  font-family: "Poppins", sans-serif;
}
.design__right {
  padding-top: 3rem;
  max-width: 50%;
  box-sizing: border-box;
  padding-inline: 5%;
}
.design__title {
  font-size: 6em;
  margin-bottom: 30px;
  font-family: "Roboto", sans-serif;
  margin-top: 2rem;
}
.design__p {
  font-size: 1.8em;
  line-height: 3rem;
}
.design__list {
  font-size: 1.8em;
  padding-left: 0;
  line-height: 4.7rem;
  margin-top: 2.5rem;
}
.design__listItem {
  list-style-type: none;
}
.design__listItem::before {
  content: "";
  background: #ff4363;
  display: inline-block;
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 100%;
  margin-right: 1em;
}
.design__button {
  font-family: "Roboto", sans-serif;
  font-size: 2.4rem;
  font-weight: bold;
  padding: 0.8em 1.6em;
  margin-top: 0.8em;
}

.faq {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  font-family: "Poppins", sans-serif;
  align-items: flex-end;
  margin-block: 5%;
  margin-inline: 13.5%;
}
.faq__left {
  width: 50%;
}
.faq__right {
  width: 50%;
  text-align: center;
}
.faq__title {
  font-size: 6rem;
  margin-top: 2rem;
  margin-bottom: 3rem;
  font-family: "Roboto", sans-serif;
}
.faq__subtitle {
  font-size: 1.8rem;
  margin-bottom: 4rem;
}
.faq__accordion {
  margin-top: 10px;
}
.faq__accordion__question {
  color: #ff4363;
  font-size: 2.4rem;
  margin-bottom: 2rem;
}
.faq__accordion__answer {
  font-size: 1.6rem;
}

.statistics {
  color: white;
  width: 73%;
  margin-block: 6%;
  margin-inline: auto;
}
.statistics .statistics__container {
  background: #ff4363;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  box-sizing: border-box;
  padding-inline: 8.5%;
  border-radius: 10px;
  padding-bottom: 5rem;
}
.statistics .statisticsBox {
  box-sizing: border-box;
  padding: 3em 0;
  text-align: center;
}
.statistics .statisticsBox__title {
  font-size: 6rem;
  font-family: "Roboto", sans-serif;
  margin-top: 6rem;
  margin-bottom: 0;
}
.statistics .statisticsBox__desc {
  font-size: 1.8rem;
  font-weight: bold;
}

.contactUs {
  width: 40%;
  margin-inline: auto;
  text-align: center;
}
.contactUs__title {
  font-size: 6rem;
  font-family: "Roboto", sans-serif;
  margin-bottom: 3rem;
}
.contactUs__p {
  font-size: 1.8rem;
  font-family: "Poppins", sans-serif;
}
.contactUs .btn {
  margin-top: 2rem;
  font-size: 2.4rem;
  padding: 2rem 4rem;
}

.header {
  display: none;
}