.advantages {
    font-family: $poppins;
    display:flex;
    flex-wrap: wrap;
    place-content: center;
    place-items: center;
    grid-template-columns: 1fr 1fr 1fr;
    margin-top:7%;
    gap:2%;

    .advantagesBox {
        text-align: center;
        width:23%;
        padding-top: 6%;
    box-sizing: border-box;
    box-shadow: 0px 0px 30px 0px #707070;
    border-radius:10px;


        &__title {
            font-size: 2.4rem;
            margin-top: 9%;
            font-weight: 700;
            margin-bottom: 10px;
        }

        &__p {
            font-size: 1.6em;
            line-height: 3em;
            width: 70%;
            margin-inline: auto;
            margin-top: 0;
        }
    }
}